.about-block {
    &_pretitle {
        color: $grey;
        &-text {
            font-size: 1.25rem;
            text-transform: uppercase;
        }
        &-icon {
            width: 2.56rem;
            height: 2.56rem;
            position: relative;
            display: block;
            margin: 0 auto;
            margin-bottom: .8rem;
            img {
                position: absolute;
                left: 0;
                top: 0;
                width: 100%;
                height: 100%;
                object-fit: contain;
            }
        }
    }
    &_title {
        padding: 2.3rem 11% 0;
    }
    &_image {
        position: relative;
        width: 48%;
        padding-top: 9rem;
        img {
            width: 100%;
            height: auto;
            display: block;
        }
        &-bg {
            position: absolute;
            left: 50%;
            top: 0;
            transform: translateX(-50%);
            white-space: nowrap;
            z-index: -1;
            font-size: 12.5rem;
            font-family: 'Cormorant Garamond';
            text-transform: uppercase;
            font-weight: bold;

            @supports ( (-webkit-background-clip: text) or (background-clip: text)) {
                background: linear-gradient(90deg, #fff,#dedede);
                text-shadow: 0px 2px 2px rgba($white,0.2);
                color: $grey;
                -webkit-background-clip: text;
                -webkit-text-fill-color: transparent;
            }
        }
    }
    &_wrap {
        display: flex;
        flex-wrap: wrap;
        margin-top: -8rem;
        position: relative;
    }
    &_list-title {
        padding: 1.25rem;
        color: $grey-3;
        font-size: 1.25rem;
        line-height: 2.25rem;
    }
    &_list {
        padding: 0 0 1.25rem 1.25rem;
        font-family: 'Cormorant Garamond';
        font-size: 2.18rem;
        &-wrap {
            background-color: $white;
            box-shadow: 3.75rem 2rem 6.25rem rgba($black, 0.03);
        }
        &-item {
            padding: 1.63rem 0;
            border-top: 1px solid $grey;
        }
    }
    &_text {
        padding-left: 30%;
        text-align: right;
        font-size: 1.88rem;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
        height: 100%;
        color: $grey-2;
        font-weight: 300;
        font-style: italic;
    }
    &_link {
        padding-top: 3.6rem;
        text-transform: uppercase;
        
        a {
            color: $orange;
            font-size: 2.25rem;
            padding-right: 5rem;
            position: relative;
            &::after {
                content: '→';
                color: $white;
                display: flex;
                justify-content: center;
                align-items: center;
                position: absolute;
                font-size: 1.5rem;
                line-height: 0;
                right: 0;
                top: 50%;
                transform: translateY(-50%);
                width: 3.56rem;
                height: 3.56rem;
                border-radius: 100%;
                background-color: $orange;
            }
        }
    }
}

